import React, { Component } from "react";
import { HashRouter as Router, Route, Link } from "react-router-dom";

import "react-bulma-components/dist/react-bulma-components.min.css";
import { Container, Heading, Hero, Navbar } from "react-bulma-components";

import DukeTable from "./components/DukeTable";
import UNCTable from "./components/UNCTable";
import WakeMedTable from "./components/WakeMedTable";
import BubbleChart from "./components/BubbleChart";
import GroupChart from "./components/GroupChart";

function Index() {
  return (
    <div>
      <h2>Home</h2>
    </div>
  );
}

class App extends Component {
  getNav() {
    return (
      <div>
        <Navbar>
          <Navbar.Item>
            <Link to="/">Home</Link>
          </Navbar.Item>
          <Navbar.Item>
            <Link to="/chart">Bubble Chart</Link>
          </Navbar.Item>
          <Navbar.Item>
            <Link to="/chart/group">Group Chart</Link>
          </Navbar.Item>
          <Navbar.Item>
            <Link to="/data/duke">Duke Data</Link>
          </Navbar.Item>
          <Navbar.Item>
            <Link to="/data/unc">UNC Data</Link>
          </Navbar.Item>
          <Navbar.Item>
            <Link to="/data/wakemed">Wakemed Data</Link>
          </Navbar.Item>
        </Navbar>
      </div>
    );
  }

  render() {
    return (
      <Router>
        <div>
          <Container>
            <Hero color="primary" gradient>
              <Hero.Body>
                <Container>
                  <Heading>Triangle Medical Costs</Heading>
                  <Heading subtitle size={3}>
                    Comparing average medical costs across the Triangle's
                    largest hospitals.
                  </Heading>
                </Container>
              </Hero.Body>
            </Hero>

            {this.getNav()}

            <Route path="/" exact component={Index} />
            <Route path="/chart" exact component={BubbleChart} />
            <Route path="/chart/group" exact component={GroupChart} />
            <Route path="/data/duke" exact component={DukeTable} />
            <Route path="/data/unc" exact component={UNCTable} />
            <Route path="/data/wakemed" exact component={WakeMedTable} />
          </Container>
        </div>
      </Router>
    );
  }
}

export default App;
